@import 'styles-mixin.scss';

body {
  font-family: $fontname;
  background-image: repeating-linear-gradient(0deg, green, green 3px, rgb(0, 100, 0) 3px, rgb(0, 100, 0) 6px);
}

input {
  font-family: $fontname;
  margin-bottom: $input-margin-bottom;
}

button {
  font-family: $fontname;
}

.content-border {
  border: $border;
  border-radius: $border-radius;
  display: flexbox;
  text-align: center;
}

.content-no-border, .content-border {
  background-color: lightgray;
  box-sizing: border-box;
  font-size: 3vw;
  // display: flexbox;
  margin-top: $margins;   //calc($margins * 4);
  margin-bottom: $margins;  //calc($margins * -2);
  margin-left: $margins;
  margin-right: $margins;
  opacity: 85%;
  padding: $padding;
  // height: calc(100% - $margins);
  // width: calc(100% - $margins);
}

.section-title {
  font-weight: bold;
  padding: $padding;
  text-align: center;
}


/* ================================================================================ */

@media (orientation: landscape) {

  @mixin create_gtp_cell_landscape($width-indicator) {
    .gtp-cell-#{$width-indicator}, .gtp-cell-plain-#{$width-indicator} {
      background-color: transparent;
      padding: $padding;
      width: percentage(100 / ($width-sections / $width-indicator));
      word-break: $word-break-type;
    }

    .gtp-cell-#{$width-indicator} {
      border: 1px solid #c3c3c3;
    }
  }

  @include create_gtp_cell_landscape(1);
  @include create_gtp_cell_landscape(2);
  @include create_gtp_cell_landscape(3);
  @include create_gtp_cell_landscape(4);
  @include create_gtp_cell_landscape(5);
  @include create_gtp_cell_landscape(6);
  @include create_gtp_cell_landscape(7);
  @include create_gtp_cell_landscape(8);
  @include create_gtp_cell_landscape(9);
  @include create_gtp_cell_landscape(10);
  @include create_gtp_cell_landscape(11);
  @include create_gtp_cell_landscape(12);

  .gtp-cell-none {
    visibility: hidden;
  }
}

/* -------------------------------------------------------------------------------- */

@media (orientation: portrait) {

  @mixin create_gtp_cell_portrait($width-indicator, $width-length) {
    .gtp-cell-#{$width-indicator}-phone, .gtp-cell-plain-#{$width-indicator}-phone {
      background-color: transparent;
      padding: $padding;
      width: $width-length;
      word-break: $word-break-type;
    }

    .gtp-cell-#{$width-indicator} {
      border: 1px solid #c3c3c3;
    }
  }

  @include create_gtp_cell_portrait(1, '50%');
  @include create_gtp_cell_portrait(2, '50%');
  @include create_gtp_cell_portrait(3, '50%');
  @include create_gtp_cell_portrait(4, '100%');
  @include create_gtp_cell_portrait(5, '100%');
  @include create_gtp_cell_portrait(6, '100%');
  @include create_gtp_cell_portrait(7, '100%');
  @include create_gtp_cell_portrait(8, '100%');
  @include create_gtp_cell_portrait(9, '100%');
  @include create_gtp_cell_portrait(10, '100%');
  @include create_gtp_cell_portrait(11, '100%');
  @include create_gtp_cell_portrait(12, '100%');

  .gtp-cell-none-phone {
    visibility: hidden;
  }
}

/* ================================================================================ */

@media (orientation: landscape) {

  .box {
    height: 200px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }

  .box>* {
    flex: 0 0 33%;
  }

}

/* -------------------------------------------------------------------------------- */

@media (orientation: portrait) {

  .box {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-top: 0.5em;
  }

  .box>* {
    flex: 0 0 10%;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-control {
    width: 95%;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-stacked {
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-center {
    text-align: center;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-right {
    text-align: right;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-full-width {
    width: 100%;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-url {
    background-color: #f0f0f0;
    color: darkblue;
    margin-left: 5px;
    z-index: 200;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  input[readonly]
  {
      background-color: #f5f5f5;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-spacing-default, .gtp-cell {
    padding: 5px;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-table {
    width: 100%;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-row, .gtp-row-plain, .gtp-inline {
    display: flex;
    flex-direction: row;
    -webkit-flex-direction: row;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-row:nth-child(even) {
    @include gtp-row-even-mixin;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-row:nth-child(odd) {
    @include gtp-row-odd-mixin;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .gtp-cell-padding {
    padding: $padding;
  }

}

/* ================================================================================ */

@media (orientation: landscape) {

  .nav-button {
    max-width: 150px;
  }

}

/* ================================================================================ */

.highlight-mystery-of-day {
  background-color: yellow;
  border-width: 2px;
  color: darkblue;
}

/* ================================================================================ */

@media (orientation: landscape) {

  .section-row {
    padding: $padding;
  }
}

